import * as am4charts from '@amcharts/amcharts4/charts';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_dark from '@amcharts/amcharts4/themes/dark';
import am4themes_dataviz from '@amcharts/amcharts4/themes/dataviz';
import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { config } from 'src/app/shared/app.config';

@Component({
  selector: 'app-fleet-utilization-chart',
  templateUrl: './fleet-utilization-chart.component.html',
  styleUrls: ['./fleet-utilization-chart.component.scss'],
})
export class FleetUtilizationChartComponent implements OnChanges {
  @Input() chartData: {
    date: string;
    value: number;
    displayLabel: string;
  }[] = [];
  private chartRef: am4charts.XYChart = null;
  @Input() dateFormat = 'dd MMM';

  ngOnChanges(simple: SimpleChanges) {
    // check if current value is changed
    if (simple['chartData'].currentValue != simple['chartData'].previousValue) {
      if (this.chartRef) {
        this.chartRef.dispose();
      }
      this.createStackedChart();
    }
  }

  constructor() {}

  createStackedChart() {
    am4core.ready(() => {
      am4core.addLicense(config.amChartLicense);

      // Themes begin
      am4core.useTheme(am4themes_animated);
      am4core.useTheme(am4themes_dataviz);
      am4core.useTheme(am4themes_dark);
      // Themes end

      // Create chart instance
      this.chartRef = am4core.create('chartdiv', am4charts.XYChart);
      this.chartRef.colors.list = [am4core.color('#B45C08')];
      // Add data

      this.chartRef.data = this.chartData;
      // Create axes
      const categoryAxis = this.chartRef.xAxes.push(new am4charts.CategoryAxis());
      categoryAxis.dataFields.category = 'date';
      categoryAxis.renderer.grid.template.location = 0;
      categoryAxis.renderer.minGridDistance = 10;
      categoryAxis.renderer.grid.template.stroke = am4core.color('#d5d7db00');
      // Configure labels
      categoryAxis.renderer.labels.template.fill = am4core.color('#4D4D4D');

      // Setting up label rotation
      categoryAxis.renderer.labels.template.rotation = 90;
      categoryAxis.renderer.labels.template.horizontalCenter = 'left';
      categoryAxis.renderer.labels.template.verticalCenter = 'middle';

      const valueAxis = this.chartRef.yAxes.push(new am4charts.ValueAxis());
      valueAxis.renderer.grid.template.stroke = am4core.color('#d5d7db');
      valueAxis.renderer.grid.template.strokeDasharray = '4'; // Configure labels
      valueAxis.renderer.labels.template.fill = am4core.color('#4D4D4D');
      valueAxis.min = 0;
      valueAxis.maxPrecision = 0;
      valueAxis.max = 100;
      valueAxis.renderer.labels.template.adapter.add('text', (text) => {
        return text + '%';
      });

      // Create series
      const series = this.chartRef.series.push(new am4charts.ColumnSeries());
      series.dataFields.valueY = 'value';
      series.dataFields.categoryY = 'displayLabel';
      series.dataFields.categoryX = 'date';
      series.name = 'Value';
      series.stacked = true;
      series.columns.template.width = am4core.percent(30);
      series.columns.template.column.cornerRadiusTopLeft = 10;
      series.columns.template.column.cornerRadiusTopRight = 10;
      series.columns.template.tooltipText = '{date}: [bold]{valueY}[/]%';

      if (this.dateFormat && this.dateFormat.length > 0 && this.dateFormat.indexOf('EEE') > -1) {
        categoryAxis.renderer.labels.template.adapter.add('text', (text, target) => {
          if (target && target.dataItem && target.dataItem.dataContext && (target.dataItem.dataContext as any).date) {
            const dateValue = target.dataItem.dataContext as any;
            // eslint-disable-next-line no-undef
            return dateValue.date.split('/')[1] + ':' + dateValue.date.split('/')[2];
          }
          return text;
        });
        series.columns.template.adapter.add('tooltipText', (text, target) => {
          if (target && target.dataItem && target.dataItem.dataContext && (target.dataItem.dataContext as any).date) {
            const dateValue = target.dataItem.dataContext as any;
            // eslint-disable-next-line no-undef
            return dateValue.date.split('/')[1] + ':' + dateValue.date.split('/')[2] + ': ' + dateValue.value + '%';
          }
          return text;
        });
      }
      // Drop-shaped tooltips
      this.createTooltipOptions(series);

      // this.chartRef.cursor = new am4charts.XYCursor();
    });
  }

  createTooltipOptions = (series: am4charts.ColumnSeries) => {
    series.tooltip.background.cornerRadius = 10;
    series.tooltip.background.strokeOpacity = 0;
    series.tooltip.pointerOrientation = 'vertical';
    series.tooltip.label.minWidth = 40;
    series.tooltip.label.minHeight = 40;
    series.tooltip.label.textAlign = 'middle';
    series.tooltip.background.fill = am4core.color('#232329');
    series.tooltip.getFillFromObject = false;
  };
}
