import { AfterViewInit, Component, ElementRef, Input, OnChanges, ViewChild } from '@angular/core';
import { H3HelperService } from 'src/app/shared/services/h3-helper/h3-helper.service';
import { MapLoaderService } from 'src/app/shared/services/map-loader/map-loader.service';
import { DARK_MODE_MAP } from 'src/assets/custom/map-themes';
import { environment } from 'src/environments/environment';
import { ITripDemandStatisticsQueryParam } from '../../models/dashboard.interface';
import { DemandLoaderService, DemandType, DemandTypes } from '../../services/demand-loader.service';

@Component({
  selector: 'app-trip-demand-map',
  templateUrl: './trip-demand-map.component.html',
  styleUrls: ['./trip-demand-map.component.scss']
})
export class TripDemandMapComponent implements AfterViewInit, OnChanges {
  readonly mapTitle: string = "Experimental"
  readonly demandUrl: string = environment.APIUrl + 'RideDispatchCenter/RideAnalytics/GetTripDemandStatistics';
  readonly geocodeUrl: string = environment.APIUrl + 'RideDispatchCenter/RideAnalytics/Geocode';

  @ViewChild('gmap') gmapElement: ElementRef;
  @ViewChild('demandTypeButtonsContainer') demandTypeButtonsContainer: ElementRef;
  @ViewChild('mapTitleElement') mapTitleElement: ElementRef;

  @Input() query: ITripDemandStatisticsQueryParam

  infoTooltip: google.maps.InfoWindow;
  defaultLocation = { lat: 25.8007, lng: 55.9762 };
  demandType: DemandType = DemandTypes.Pickup;

  pickupActive: boolean = false;
  dropoffActive: boolean = false;

  map!: google.maps.Map;
  demandLoader!: DemandLoaderService;

  constructor(public mapLoader: MapLoaderService) { }

  ngAfterViewInit(): void {
    this.mapLoader.initMap(this.gmapElement, this.defaultLocation.lat, this.defaultLocation.lng)
      .then(async () => {
        this.infoTooltip = new google.maps.InfoWindow();
        this.map = this.mapLoader.map;

        this.map.setOptions({
          styles: DARK_MODE_MAP,
          zoom: 13,
          minZoom: 9,
          maxZoom: 15,
          mapTypeControl: false,
          streetViewControl: false,
          fullscreenControl: true,
        });

        this.demandTypeButtonsContainer.nativeElement.style.display = "inline-flex";
        this.map.controls[google.maps.ControlPosition.TOP_LEFT].push(this.demandTypeButtonsContainer.nativeElement);
        this.map.controls[google.maps.ControlPosition.BOTTOM_LEFT].push(this.mapTitleElement.nativeElement);

        this.demandLoader = new DemandLoaderService(this.map, {
          baseUrl: this.demandUrl,
          dataLayerConfigurer: this.onConfigureDataLayer.bind(this),
          defaultDemandType: this.demandType,
          query: this.query,
        });

        await this.demandLoader.loadDemand();
        this.onDemandTypeChanged()
      });
  }

  ngOnChanges() {
    if (this.demandLoader) {
      this.demandLoader.loadDemand();
      this.onDemandTypeChanged()
    }
  }

  public setDemandType(demandType: DemandType) {
    this.demandType = demandType;
    this.onDemandTypeChanged();
  }

  private onConfigureDataLayer(dataLayer: google.maps.Data) {
    dataLayer.addListener("click", (event) => {
      const currentDemandType = this.demandLoader.getDemandType();

      const property = event.feature.getProperty(currentDemandType);
      if (!property) return;

      const cellPos = H3HelperService.GetH3CellLatLng(event.feature.getProperty("cell"));
      this.infoTooltip.setPosition(cellPos);

      let content = `
              <div class="text-white text-xl mb-2 overflow-clip whitespace-normal overflow-hidden" style="width: 200px;">
                Fetching address...
              </div>
              <div class="text-white text-lg">
                  Total ${this.GetDemandTypeLabel(currentDemandType)}
                  <span class="px-1 py-1 font-medium z-10 text-orange-500">${property.count}</span>
              </div>
          `;

      this.geocode(cellPos.lat, cellPos.lng)
        .then(address => {
          this.infoTooltip.setContent(content.replace("Fetching address...", address));
        });

      this.infoTooltip.setContent(content);
      this.infoTooltip.open(this.map);
    });

    dataLayer.addListener("mouseout", (_event) => {
      this.infoTooltip.close();
    });
  }

  private GetDemandTypeLabel(currentDemandType: DemandType) {
    switch (currentDemandType) {
      case DemandTypes.Pickup:
        return "Pickups";
      case DemandTypes.Dropoff:
        return "Drop-Offs";
    }
  }

  private onDemandTypeChanged() {
    this.demandLoader.setDemandType(this.demandType);
    this.pickupActive = this.demandType == DemandTypes.Pickup;
    this.dropoffActive = this.demandType == DemandTypes.Dropoff;
  }

  private async geocode(lat: number, lng: number) {
    const params = new URLSearchParams({
      lat: lat.toString(),
      lng: lng.toString(),
    });

    const response = await fetch(`${this.geocodeUrl}?${params.toString()}`);
    if (!response.ok) return "Address could not be fetched";

    const data = await response.json();

    if (!data.successStatus) return "Address not found";

    return data.address;
  }
}
