import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import {
  IAreaStatisticsResult,
  IAreaStatisticsQueryParam,
  IDriverStatisticsQueryParam,
  IRideStatistics,
  IRideStatisticsQueryParam,
  IVehicleFranchise,
  IAreaGroupsResult,
} from '../models/dashboard.interface';
import { IDriverStatistics } from './../models/dashboard.interface';

@Injectable({
  providedIn: 'root',
})
export class DashboardService {
  private readonly rideAnalyticEndpoint = environment.APIUrl + 'RideDispatchCenter/RideAnalytics/';

  httpHeader: HttpHeaders;

  constructor(private http: HttpClient) {
    this.httpHeader = new HttpHeaders({
      'X-API-KEY': environment.rideSharingApiKey,
      accept: '*/*',
    });
  }

  getVehicleFranchises() {
    return this.http.get<IVehicleFranchise>(`${this.rideAnalyticEndpoint}GetVehicleFranchises`);
  }

  getDriverStatistics(driverQueryParams: IDriverStatisticsQueryParam) {
    return this.http.get<IDriverStatistics>(`${this.rideAnalyticEndpoint}GetDriverStatistics`, {
      params: {
        ...driverQueryParams,
      },
    });
  }

  getDriverImage(staffId: string) {
    return this.http.get<{ DriverImage: string; DriverName: string }>(
      `${this.rideAnalyticEndpoint}GetDriverImage?staffId=${staffId}`
    );
  }

  getTripStatistics(ridestatistics: IRideStatisticsQueryParam) {
    return this.http.get<IRideStatistics>(`${this.rideAnalyticEndpoint}GetTripStatistics`, {
      params: {
        ...ridestatistics,
      },
    });
  }

  getAreaStatistics(areaStatisticsQuery: IAreaStatisticsQueryParam) {
    return this.http.get<IAreaStatisticsResult>(`${this.rideAnalyticEndpoint}GetAreaStatistics`, {
      params: {
        ...areaStatisticsQuery,
      },
    });
  }
}
