import * as h3 from "h3-js";

export class H3HelperService {
  private readonly defaultConfig: IH3HelperConfig = {
    style: {
      fillOpacity: 0,
      strokeColor: "lightgrey",
      strokeWeight: 0.1,
    },
    defaultGroupVisibility: true
  };

  private config: IH3HelperConfig;

  private dataLayers: Map<string, google.maps.Data> = new Map<string, google.maps.Data>();

  /**
   * Creates a new H3HelperService instance
   * @param {google.maps.Map} map
   * @param {IH3HelperConfig|object} options
   */
  constructor(public map: google.maps.Map, options: IH3HelperConfig | object) {
    this.config = Object.assign({}, this.defaultConfig, options);
  }

  /**
   * Renders an array of cells on the map's default data layer
   * @param {string[]|IH3HelperCellLiteral[]} cells - An array of strings or an array of `IH3HelperCellLiteral` objects
   * @returns {google.maps.Data.Feature[]}
   */
  renderCells(cells: string[] | IH3HelperCellLiteral[]): google.maps.Data.Feature[] {
    const features = cells
      .map((cell: string | IH3HelperCellLiteral) => this.getCell(cell))
      .map(cellInfo => H3HelperService.convertH3CellToGeoJson(cellInfo.cell, cellInfo.properties));

    const geoJson = {
      type: "FeatureCollection",
      features: features
    }

    this.map.data.setStyle(this.config.styleFunction ?? this.config.style);

    return this.map.data.addGeoJson(geoJson);
  }

  /**
   * Renders an array of cells on a new data layer
   * @param {string} groupId - A string that can be used later to reference the data layer in order to show/hide/remove it
   * @param {string[]|IH3HelperCellLiteral[]} cells - An array of strings or an array of `IH3HelperCellLiteral` objects
   * @param {Function} dataLayerConfigFunc - A function that receives the data layer object for extra configuration (events, etc.)
   * @returns {void}
   */
  renderCellsAsGroup(groupId: string, cells: string[] | IH3HelperCellLiteral[], dataLayerConfigFunc: Function = null): void {
    if (this.dataLayers.has(groupId)) {
      this.removeGroup(groupId);
    }

    const features = cells
      .map((cell: string | IH3HelperCellLiteral) => this.getCell(cell))
      .map(cellInfo => H3HelperService.convertH3CellToGeoJson(cellInfo.cell, cellInfo.properties));

    const dataLayer = new google.maps.Data();
    dataLayer.addGeoJson({
      type: "FeatureCollection",
      features: features
    });

    dataLayer.setStyle(this.config.styleFunction ?? this.config.style);

    this.dataLayers.set(groupId, dataLayer)

    dataLayerConfigFunc?.(dataLayer);

    this.setGroupVisibility(groupId, this.config.defaultGroupVisibility);
  }

  /**
   * Changes the visibility of a data layer by removing it from the map or adding it back
   * @param {string} groupId - A string that references the data layer
   * @param {boolean} visible - A boolean that indicates whether the data layer should be visible or not
   * @returns {void}
   */
  setGroupVisibility(groupId: string, visible: boolean): void {
    const dataLayer = this.dataLayers.get(groupId);
    if (!dataLayer)
      throw new Error("[H3Helper] group does not exist");

    if (visible) {
      dataLayer.setMap(this.map);
    } else {
      dataLayer.setMap(null);
    }
  }

  /**
   * Removes a data layer from the map and deletes it from the internal list
   * @param {string} groupId - A string that references the data layer
   * @returns {void}
   */
  removeGroup(groupId: string): void {
    let dataLayer = this.dataLayers.get(groupId);
    if (dataLayer) {
      dataLayer.setMap(null);
      this.dataLayers.delete(groupId);
      dataLayer = null;
    }
  }

  /**
   * Refreshes a data layer's style
   * @param {string} groupId - A string that references the data layer
   */
  refreshGroupStyle(groupId: string) {
    const dataLayer = this.dataLayers.get(groupId);
    if (!dataLayer)
      throw new Error("[H3Helper] group does not exist");
    dataLayer.setStyle(this.config.styleFunction ?? this.config.style);
  }

  /**
   * Retrieves the current map bounds as an array of H3 cells
   * @param {number} resolution - An optional parameter that specifies the resolution of the returned cells, if not specified, the current map resolution will be used instead
   * @returns {string[]}
   */
  getMapBoundsAsCells(resolution: number = null): string[] {
    resolution = resolution ?? this.getMapResolution();
    const bounds = this.map.getBounds();
    const ne = bounds.getNorthEast();
    const sw = bounds.getSouthWest();

    const cells = h3.polygonToCells([
      [ne.lat(), ne.lng()],
      [ne.lat(), sw.lng()],
      [sw.lat(), sw.lng()],
      [sw.lat(), ne.lng()],
      [ne.lat(), ne.lng()],
    ], resolution);

    return cells;
  }

  /**
   * Retrieves the current map resolution
   * @returns {number}
   */
  getMapResolution(): number {
    return H3HelperService.convertZoomToResolution(this.map.getZoom())
  }

  private getCell(obj: string | IH3HelperCellLiteral): IH3HelperCellLiteral {
    if (typeof obj === "string") {
      return { cell: obj, properties: undefined };
    } else {
      return { cell: obj.cell, properties: obj.properties };
    }
  }

  /**
   * Converts an H3 cell to a specific resolution (returned as an array of cells)
   * @param {string} cell - The H3 cell to be converted
   * @param {number} resolution - The resolution of the resulting cell(s)
   * @returns {string[]}
   */
  static convertH3CellToResolution(cell: string, resolution: number): string[] {
    const cellRes = h3.getResolution(cell);

    if (cellRes == resolution)
      return [cell];
    else if (cellRes > resolution)
      return [h3.cellToParent(cell, resolution)];
    else
      return h3.cellToChildren(cell, resolution);
  }

  /**
   * Converts an H3 cell to a GeoJSON object
   * @param {string} cell - The H3 cell to be converted
   * @param {object} properties - An optional object that will be added to the GeoJSON object as properties, by default, the cell string will be added as a property `cell`
   * @returns {object}
   */
  static convertH3CellToGeoJson(cell: string, properties: object = null): GeoJSON.Feature {
    return {
      type: "Feature",
      geometry: {
        type: "Polygon",
        coordinates: [h3.cellToBoundary(cell, true)],
      },
      properties: Object.assign({
        cell: cell
      }, properties)
    };
  }

  /**
   * Converts a map zoom level to an h3 resolution
   * @param {number} zoom - The map zoom level
   * @returns {number}
   */
  static convertZoomToResolution(zoom: number): number {
    if (zoom <= 11)
      return 6;
    else if (zoom <= 12)
      return 7;
    else if (zoom <= 13)
      return 8;
    else if (zoom <= 14)
      return 9;
    else if (zoom <= 15)
      return 10;
    else // > 15
      return 10;
  }

  /**
   * Retrieves the center of an H3 cell as a LatLng object
   * @param {string} cell - The H3 cell
   */
  static GetH3CellLatLng(cell: string): google.maps.LatLngLiteral {
    const pos = h3.cellToLatLng(cell);
    return { lat: pos[0], lng: pos[1] }
  }
}

export interface IH3HelperConfig {
  style?: google.maps.Data.StyleOptions;
  styleFunction?: google.maps.Data.StylingFunction;
  defaultGroupVisibility?: boolean;
}

export interface IH3HelperCellLiteral {
  cell: string;
  properties?: object;
}