/* eslint-disable no-undef */
import { formatDate } from '@angular/common';
import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { interval, Subject } from 'rxjs';
import { filter, pluck, takeUntil } from 'rxjs/operators';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import {
  IAreaStatisticsData,
  IAreaStatisticsQueryParam,
  IChartData,
  IDailySummary,
  IDriverStatisticsQueryParam,
  IRideStatisticsQueryParam,
  ISummary,
  ITimeSeries,
  ITripDemandStatisticsQueryParam,
  ITripFranchiseSummary,
  ITripSourceSummary,
  rideStatisticTimeInterval,
} from '../models/dashboard.interface';
import { AlertService } from './../../../shared/services/alert-service/alert-service.service';
import { HumanizerService } from './../../../shared/services/humanizer/humanizer.service';
import { DashboardService } from './../services/dashboard.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DashboardComponent implements OnInit, OnDestroy {
  // period
  public periodInterval: rideStatisticTimeInterval | null = rideStatisticTimeInterval.LastWeek;
  private _destroy$ = new Subject<any>();

  public periodsList = [
    {
      id: 0,
      name: '[[[[Last 24 hours]]]]',
    },
    {
      id: 1,
      name: '[[[[Last 7 days]]]]',
    },
    {
      id: 2,
      name: '[[[[Last 30 days]]]]',
    },
    {
      id: 3,
      name: '[[[[Last 90 days]]]]',
    },
    {
      id: 4,
      name: '<i class="fas fa-calendar-day text-orange-600"></i> [[[[Custom]]]]',
    },
  ];

  // demand query 
  public demandQuery: ITripDemandStatisticsQueryParam;

  public areaStatisticsQuery: IAreaStatisticsQueryParam;

  // selected period
  public selectedPeriod = 1;

  private _virtualSelectedPeriod = 1;

  // Franchise
  public franchisesList = [];
  public selectedFranchise = null;

  // Interval
  public selectedInterval = 'Monthly';

  // rideSummary Object of type ISummary
  public rideSummary: ISummary;

  // charts Data
  public incomeChartData: IChartData[] = [];
  public ridesChartData: IChartData[] = [];
  public fleetUtilizationData: IChartData[] = [];
  public completedRideData: { name: string; value: number }[] = [];

  public areasData: IAreaStatisticsData[] = [];

  // summary
  public dailySummary: IDailySummary;
  public franchiseSummary: ITripFranchiseSummary[];

  public showCalendar = false;

  // drivers List
  public topIncomingDrivers: {
    driverNumber: string;
    driverName: string;
    driverImage: string;
    driverRating: number;
  }[] = [];

  public topTripDrivers: {
    driverNumber: string;
    driverName: string;
    driverImage: string;
    driverRating: number;
    driverAbbreviation?: number;
  }[] = [];

  public currencySymbol = this.session.currentTenant.currency;
  // loading state
  public isLoading = false;

  // driver loading state
  public isDriverLoading = false;

  // areas loading state
  public isAreaLoading = false;

  // dateFormat
  public dateFormat = 'dd/MM/yyyy';

  // range datepicker startDate
  startDate = {
    display: formatDate(new Date(), 'd MMM EEEE', this.session.locale),
    value: new Date(),
  };

  // range datepicker endDate
  endDate = {
    display: formatDate(new Date(), 'd MMM EEEE', this.session.locale),
    value: new Date(),
  };
  dateRangeChanged = false;
  showDateValidation = false;
  constructor(
    private dashboardService: DashboardService,
    private session: SessionService,
    private alertService: AlertService,
    private humanizer: HumanizerService
  ) { }

  ngOnInit(): void {
    this.franchisesList = [{ id: null, name: 'All' }];
    this.getVehicleFranchises();
    this.onPeriodChange(this.periodsList[1].id ?? 1);

    // recall the api every 10 min
    interval(600000)
      .pipe(takeUntil(this._destroy$))
      .subscribe(() => {
        this.getRideStatistics();
        this.updateDemandQuery();
      });
  }

  ngOnDestroy(): void {
    this._destroy$.next();
    this._destroy$.complete();
  }

  getVehicleFranchises() {
    this.dashboardService
      .getVehicleFranchises()
      .pipe(pluck('Franchises'))
      .subscribe({
        next: (data) => {
          data.forEach((item) =>
            this.franchisesList.push({
              id: item.Id,
              name: item.Name,
            })
          );
        },
      });
  }

  getFranchiseName(franchiseId: number) {
    const franchise = this.franchisesList.find((f) => f.id === franchiseId);
    return franchise ? franchise.name : 'All';
  }

  getFormattedDate(date: Date) {
    return formatDate(date, 'dd/MM/yyyy', this.session.locale);
  }

  getRideStatistics() {
    this.isLoading = true;
    this.topIncomingDrivers = [];
    this.topTripDrivers = [];
    const startDate = this.getFormattedDate(this.startDate.value);
    const endDate = this.getFormattedDate(this.endDate.value);

    const params: IRideStatisticsQueryParam = {
      timeZoneId: this.session.timeZone,
      franchiseName: this.getFranchiseFilter(),
    };

    if (this.periodInterval) {
      params.periods = this.periodInterval;
    } else {
      params.from = startDate;
      params.to = endDate;
    }

    this.dashboardService.getTripStatistics(params).subscribe({
      next: (data) => {
        const result = data?.result;

        if (result) {
          this.selectedInterval = data.interval;
          this.completedRideData = result.tripSourceSummary.map((item) => ({
            name: this.humanizer.transform(item.name),
            value: item.tripCount,
          }));
          this.dailySummary = result.dailySummary;
          this.rideSummary = {
            ...result.summary,
          };
          this.franchiseSummary = result.franchiseSummary;
          if (result.timeSeries?.length > 0)
            this.getDriverStatistics(result.timeSeries[0].time, result.timeSeries[result.timeSeries.length - 1].time);
          else
            this.getDriverStatistics(startDate, endDate);
          this.mapChartValues(result);
        } else {
          this.isLoading = false;
        }
      },
      error: (err) => {
        this.isLoading = false;
        console.log(err);
        if (err.error.message) {
          this.alertService.showAlert('warning', err.error.message, 'error');
        } else {
          const errorValues = Object.values(err.error.errors).join('<br>');
          this.alertService.showAlert('error', errorValues, 'error');
        }
      },
      complete: () => {
        this.isLoading = false;
      },
    });
  }

  updateDemandQuery() {
    this.demandQuery = {
      timeZoneId: this.session.timeZone,
      from: this.getFormattedDate(this.startDate.value),
      to: this.getFormattedDate(this.endDate.value),
      franchiseName: this.getFranchiseFilter(),
      periods: this.periodInterval,
      resolution: 9,
    };
  }

  updateAreaStatisticsQuery(){
    const startDate = this.getFormattedDate(this.startDate.value);
    const endDate = this.getFormattedDate(this.endDate.value);

    const params: IAreaStatisticsQueryParam = {
      timeZoneId: this.session.timeZone,
      franchiseName: this.getFranchiseFilter(),
    };

    if (this.periodInterval) {
      params.periods = this.periodInterval;
    } else {
      params.from = startDate;
      params.to = endDate;
    }

    this.areaStatisticsQuery = params
  }

  setPeriodDate(fromDate: string, toDate: string) {
    const fromDateParsed = new Date(fromDate);
    const toDateParsed = new Date(toDate);

    if (this.periodInterval === rideStatisticTimeInterval.Last3Month) {
      fromDateParsed.setDate(new Date().getDate());
      toDateParsed.setDate(new Date().getDate());
    }

    this.startDate.value = fromDateParsed;
    this.endDate.value = toDateParsed;
    this.endDate.display = formatDate(this.endDate.value, 'd MMM EEEE', this.session.locale);
    this.startDate.display = formatDate(this.startDate.value, 'd MMM EEEE', this.session.locale);
  }

  getDriverStatistics(fromDate: string, toDate: string) {
    const startDate = this.getFormattedDate(this.startDate.value);
    const endDate = this.getFormattedDate(this.endDate.value);
    this.isDriverLoading = true;
    const params: IDriverStatisticsQueryParam = {
      timeZoneId: this.session.timeZone,
      number: 5,
      franchiseName: this.getFranchiseFilter(),
    };
    if (this.periodInterval) {
      params.periods = this.periodInterval;
    } else {
      params.from = startDate;
      params.to = endDate;
    }

    this.dashboardService
      .getDriverStatistics(params)
      .pipe(filter((data) => data.topIncomeDrivers.length > 0 && data.topTripDrivers.length > 0))
      .subscribe({
        next: (res) => {
          if (this.periodInterval) this.setPeriodDate(fromDate, toDate);
          this.isDriverLoading = false;
          res.topIncomeDrivers.forEach((item) => {
            this.topIncomingDrivers.push({
              driverNumber: item.staffId,
              driverName: item.staffId,
              driverImage: '',
              driverRating: item.income,
            });

            this.getDriverImage(item.staffId, this.topIncomingDrivers);
          });

          res.topTripDrivers.forEach((item) => {
            this.topTripDrivers.push({
              driverNumber: item.staffId,
              driverName: item.staffId,
              driverImage: '',
              driverRating: item.tripCount,
            });

            this.getDriverImage(item.staffId, this.topTripDrivers);
          });
        },
        complete: () => {
          this.isDriverLoading = false;
        },
      });
  }

  getFranchiseFilter(): string | undefined {
    return this.selectedFranchise !== null ? this.franchisesList.find((f) => f.id === this.selectedFranchise)?.name : "";
  }

  getDriverImage(staffId: string, driversList: any[]) {
    this.dashboardService.getDriverImage(staffId).subscribe({
      next: (res) => {
        const driver = driversList.find((driver) => driver.driverNumber === staffId);
        if (driver) {
          const driverAbbreviation = res.DriverName.split(' ');

          driver.driverImage = res.DriverImage;
          driver.driverName = res.DriverName;
          driver.driverAbbreviation =
            driverAbbreviation[0].charAt(0) + ' ' + driverAbbreviation[driverAbbreviation.length - 1].charAt(0);
        }
      },
    });
  }

  mapChartValues(staticData: {
    summary: ISummary;
    timeSeries: ITimeSeries[];
    dailySummary: IDailySummary;
    tripSourceSummary: ITripSourceSummary[];
  }) {
    this.incomeChartData = [];
    this.ridesChartData = [];
    this.fleetUtilizationData = [];
    this.dateFormat = this.getFormatBasedOnInterval(this.selectedInterval);
    staticData.timeSeries.forEach((item) => {
      this.ridesChartData.push({
        date: formatDate(item.time, this.dateFormat, this.session.locale),
        value: item.data.tripCount,
      });

      this.incomeChartData.push({
        date: item.time,
        value: parseInt(item.data.income.toString()),
        displayName: formatDate(
          item.time,
          this.selectedInterval === 'Hourly'
            ? this.session.timeFormat == '12'
              ? 'hh:mm a'
              : 'HH:mm'
            : this.dateFormat,
          this.session.locale
        ),
      });

      this.fleetUtilizationData.push({
        date: formatDate(item.time, this.dateFormat, this.session.locale),
        value: item.data.utilization,
        displayName: item.time,
      });
    });
  }

  getFormatBasedOnInterval(interval: string) {
    let selectedFormat = '';

    switch (interval) {
      case 'Hourly': {
        const amPmFormat = this.session.timeFormat == '12' ? 'EEE/hh/mm a' : 'EEE/HH/mm';
        selectedFormat = amPmFormat;
        break;
      }
      case 'Daily': {
        selectedFormat = 'dd/MMM';
        break;
      }
      case 'Monthly': {
        selectedFormat = 'MMM/yy';
        break;
      }
    }

    return selectedFormat;
  }

  onFranchiseChange(franchise: number) {
    this.selectedFranchise = franchise;
    this.getRideStatistics();
    this.updateDemandQuery();
    this.updateAreaStatisticsQuery()
  }

  onPeriodChange(period: number, event?: { toggle: () => void }) {
    this.selectedPeriod = period;

    this.setSelectedPeriod(period);

    if (period !== 4) {
      this.resetDatepicker();
      this._virtualSelectedPeriod = period;
      // custom calendar
      event?.toggle();
      this.getRideStatistics();
      this.updateDemandQuery();
      this.updateAreaStatisticsQuery()
    } else {
      this.showCalendar = true;
      this.resetSelectedPeriod();
    }
  }

  setSelectedPeriod(period: number) {
    switch (period) {
      case 0: {
        this.periodInterval = rideStatisticTimeInterval.LastDay;
        break;
      }
      case 1: {
        this.periodInterval = rideStatisticTimeInterval.LastWeek;
        break;
      }
      case 2: {
        this.periodInterval = rideStatisticTimeInterval.LastMonth;
        break;
      }
      case 3: {
        this.periodInterval = rideStatisticTimeInterval.Last3Month;
        break;
      }
    }
  }

  resetSelectedPeriod() {
    this.periodInterval = null;
  }

  resetDatepicker() {
    this.showDateValidation = false;
    this.showCalendar = false;

    this.dateRangeChanged = false;
    this.endDate = {
      display: formatDate(new Date(), 'd MMM EEEE', this.session.locale),
      value: new Date(),
    };
    this.startDate = {
      display: formatDate(new Date(), 'd MMM EEEE', this.session.locale),
      value: new Date(),
    };
  }

  datepickerCloseHandler(event: { preventDefault: () => void }) {
    event.preventDefault();
  }

  closeCalendar(popup: any) {
    this.selectedPeriod = this._virtualSelectedPeriod;
    if (this.selectedPeriod !== 4) {
      this.showCalendar = false;
      this.setSelectedPeriod(this.selectedPeriod);
    }
    popup?.toggle();
  }

  submitDate(event: any) {
    if (this.endDate.value < this.startDate.value) {
      this.showDateValidation = true;
      return;
    }
    this._virtualSelectedPeriod = this.selectedPeriod;
    this.showDateValidation = false;
    this.endDate.display = formatDate(this.endDate.value, 'd MMM EEEE', this.session.locale);
    this.startDate.display = formatDate(this.startDate.value, 'd MMM EEEE', this.session.locale);
    event?.toggle();
    this.dateRangeChanged = true;
    this.resetSelectedPeriod();
    this.getRideStatistics();
    this.updateDemandQuery();
    this.updateAreaStatisticsQuery()
  }

  public disabledDates = (date: Date): boolean => {
    // disable all dates after today
    return date > new Date();
  };
}
