/* eslint-disable no-undef */
import { Pipe, PipeTransform } from '@angular/core';
import { SessionService } from 'src/app/shared/services/session-service/session.service';

@Pipe({
  name: 'lengthConversion',
})
export class LengthConversionPipe implements PipeTransform {
  constructor(private session: SessionService) {}

  transform(value: number, converstion: string, totalValueConvestion = false): string {
    if (!value) {
      return '0';
    }

    switch (converstion?.toLowerCase()) {
      case 'km': {
        // convert from Feet-Centimeters
        const meterToKM = (totalValueConvestion ? value * 1000 : value / 1000).toLocaleString(this.session.locale, {
          maximumFractionDigits: 3,
          minimumFractionDigits: 0,
        });
        return meterToKM;
      }
      case 'mi': {
        // convert from Feet-Centimeters
        return (totalValueConvestion ? value * 1609 : value / 1609).toLocaleString(this.session.locale, {
          maximumFractionDigits: 3,
          minimumFractionDigits: 0,
        });
      }
      default: {
        return value.toLocaleString(this.session.locale, { maximumFractionDigits: 3, minimumFractionDigits: 0 });
      }
    }
  }
}
