import * as am4charts from '@amcharts/amcharts4/charts';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_dark from '@amcharts/amcharts4/themes/dark';
import { formatDate } from '@angular/common';
import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { config } from 'src/app/shared/app.config';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import { environment } from 'src/environments/environment';
import { IAreaGroupsResult, IAreaStatisticsData, IAreaStatisticsQueryParam, IAreaStatisticsResult } from '../../models/dashboard.interface';

@Component({
  selector: 'app-areas-analytics-chart',
  templateUrl: './areas-analytics-chart.component.html',
  styleUrls: ['./areas-analytics-chart.component.scss']
})
export class AreasAnalyticsChartComponent implements OnChanges, OnInit {
  private readonly apiBaseUrl = environment.APIUrl + 'RideDispatchCenter/RideAnalytics';
  private chartRef: am4charts.XYChart = null;
  private chartData: IAreaStatisticsData[] = [];

  @Input() isLoading: boolean = false;
  @Input() query: IAreaStatisticsQueryParam | null = null;
  @Input() dateFormat = 'dd MMM';

  public areaGroupsList: { name: string, value: number }[] = [];
  public selectedAreaGroup: number;

  constructor(private session: SessionService) { }

  ngOnInit(): void {
    this.updateAreaGroups();
    this.refreshChart();
  }

  ngOnChanges(simple: SimpleChanges): void {
    if (this.query === null
      || simple['query'].currentValue == simple['query'].previousValue) {
      return;
    }
    this.refreshChart();
  }

  refreshChart() {
    this.getAreaStatistics()
      .then(() => {
        if (this.chartRef) {
          this.chartRef.dispose();
        }

        this.createHeatMapChart();
      });
  }

  updateAreaGroups() {
    this.areaGroupsList = [{ name: "All", value: -1 }]
    this.selectedAreaGroup = -1;

    return fetch(`${this.apiBaseUrl}/GetAreaGroups`)
      .then(res => res.json())
      .then((data: IAreaGroupsResult) => {
        this.areaGroupsList.push(...data.AreaGroups.map(g => ({ name: g.Name, value: g.Id })))
      });
  }

  onAreaGroupChange(areaGroup: number) {
    this.selectedAreaGroup = areaGroup;
    this.refreshChart();
  }

  getAreaGroupName(groupValue){
    const group = this.areaGroupsList.find(g => g.value === groupValue);
    return group?.name ?? 'Area Group';
  }

  getAreaStatistics() {
    const queryParams = new URLSearchParams({
      from: this.query.from,
      to: this.query.to,
      timeZoneId: this.query.timeZoneId,
      periods: this.query.periods?.toString(),
      franchiseName: this.query.franchiseName ?? ""
    });

    if (this.selectedAreaGroup && this.selectedAreaGroup !== -1) {
      queryParams.append('areaGroup', this.selectedAreaGroup.toString());
    }

    this.isLoading = true;
    return fetch(`${this.apiBaseUrl}/GetAreaStatistics?${queryParams.toString()}`)
      .then(res => res.json())
      .finally(() => this.isLoading = false)
      .then((data: IAreaStatisticsResult) => {
        data.timeSeries?.forEach(t => {
          t.time = formatDate(t.time, this.dateFormat, this.session.locale)
        });
        this.chartData = data.timeSeries ?? [];
      });
  }

  createHeatMapChart() {
    am4core.ready(() => {
      am4core.addLicense(config.amChartLicense);
      // Themes begin
      am4core.useTheme(am4themes_dark);
      am4core.useTheme(am4themes_animated);
      // Themes end

      // Create chart instance
      this.chartRef = am4core.create("areasChartDiv", am4charts.XYChart);
      this.chartRef.hiddenState.properties.opacity = 0; // this makes initial fade in effect
      this.chartRef.colors.list = [am4core.color('#d96b0f')];
      this.chartRef.maskBullets = false;

      // setup xAxis
      const xAxis = this.chartRef.xAxes.push(new am4charts.CategoryAxis());
      xAxis.dataFields.category = "time";
      xAxis.renderer.grid.template.stroke = am4core.color('#1c1c21');
      xAxis.renderer.grid.template.strokeOpacity = 0.8;
      xAxis.renderer.grid.template.disabled = true;
      xAxis.renderer.minGridDistance = 40;
      xAxis.renderer.labels.template.fill = am4core.color('#525252');
      xAxis.renderer.labels.template.wrap = true;
      xAxis.renderer.labels.template.maxWidth = 120;
      xAxis.renderer.labels.template.rotation = 90;
      xAxis.renderer.labels.template.horizontalCenter = 'left';
      xAxis.renderer.labels.template.verticalCenter = 'middle';

      // setup yAxis
      const yAxis = this.chartRef.yAxes.push(new am4charts.CategoryAxis());
      yAxis.dataFields.category = "areaName";
      yAxis.renderer.grid.template.stroke = am4core.color('#1c1c21');
      yAxis.renderer.grid.template.strokeOpacity = 0.8;
      yAxis.renderer.labels.template.fill = am4core.color('#525252');
      yAxis.renderer.labels.template.tooltipText = '{areaName}';
      yAxis.renderer.labels.template.truncate = true;
      yAxis.renderer.labels.template.align = 'left';
      yAxis.renderer.labels.template.maxWidth = 120;
      yAxis.renderer.grid.template.disabled = true;
      yAxis.renderer.inversed = true;
      yAxis.renderer.minGridDistance = 20;

      // setup series
      const series = this.chartRef.series.push(new am4charts.ColumnSeries());
      series.dataFields.categoryX = "time";
      series.dataFields.categoryY = "areaName";
      series.dataFields.value = "pickupCount";
      series.sequencedInterpolation = true;
      series.defaultState.transitionDuration = 3000;
      series.strokeWidth = 2;
      series.stroke = am4core.color('#D96B00');
      series.fill = am4core.color('#D96B00');
      series.fillOpacity = 0.9;

      // setup series columns (pickupCount)
      const columnTemplate = series.columns.template;
      columnTemplate.strokeWidth = 0;
      columnTemplate.strokeOpacity = 0;
      columnTemplate.tooltipText = "{areaName}, {time}: {pickupCount}";
      columnTemplate.width = am4core.percent(100);
      columnTemplate.height = am4core.percent(100);

      // setup series columns heat colors
      if (this.chartData.every(a => a.pickupCount == 0)) {
        series.heatRules.push({
          target: columnTemplate,
          property: "fill",
          min: am4core.color("#2b2f34", 0.6),
          max: am4core.color("#2b2f34", 0.6),
          logarithmic: true
        });
      } else {
        series.heatRules.push({
          target: columnTemplate,
          property: "fill",
          min: am4core.color("#2b2f34", 0.6),
          max: am4core.color("#d9611e"),
        });
      }

      // Create vertical scrollbar and place it before the value axis
      this.chartRef.scrollbarY = new am4core.Scrollbar();
      this.chartRef.scrollbarY.parent = this.chartRef.leftAxesContainer;
      this.chartRef.scrollbarY.toBack();

      // Create a horizontal scrollbar with previe and place it underneath the date axis
      this.chartRef.scrollbarX = new am4core.Scrollbar();
      this.chartRef.scrollbarX.parent = this.chartRef.bottomAxesContainer;

      // setup dateTime formats
      if (this.dateFormat && this.dateFormat.length > 0 && this.dateFormat.indexOf('EEE') > -1) {
        xAxis.renderer.labels.template.adapter.add('text', (text, target) => {
          const timeValue: string = (target?.dataItem?.dataContext as IAreaStatisticsData)?.time;
          if (!timeValue) return text;

          const timeValues = timeValue.split('/');
          return timeValues[1] + ':' + timeValues[2];
        });

        series.columns.template.adapter.add('tooltipText', (text, target) => {
          const values: IAreaStatisticsData = target?.dataItem?.dataContext as IAreaStatisticsData;
          if (!values?.time) return text;

          const timeValues = values.time.split('/');
          return `${values.areaName} ${timeValues[1]}:${timeValues[2]}: ${values.pickupCount}`;
        });
      }

      // set chart data
      this.chartRef.data = this.chartData;
    });
  }
}


