import { Component, Input } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-top-stats-list',
  templateUrl: './top-stats-list.component.html',
  styleUrls: ['./top-stats-list.component.scss'],
})
export class TopStatsListComponent {
  @Input() data: {
    driverNumber: string;
    driverName: string;
    driverImage: string;
    driverRating: number;
    driverAbbreviation?: string;
  }[];

  @Input() listTitle = 'Top Completed Rides';

  constructor(public sanitizer: DomSanitizer) {}
}
