/* eslint-disable no-undef */
import * as am4charts from '@amcharts/amcharts4/charts';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { config } from 'src/app/shared/app.config';
import { NumberSuffixPipe } from './../../../../shared/pipes/number-suffix/number-suffix.pipe';

@Component({
  selector: 'app-total-completed-ride',
  templateUrl: './total-completed-ride.component.html',
  styleUrls: ['./total-completed-ride.component.scss'],
  providers: [NumberSuffixPipe],
})
export class TotalCompletedRideComponent implements OnChanges {
  @Input() chartData: { name: string; value: number }[] = [];
  @Input() totalCompletedRides = 0;
  private chartRef: am4charts.PieChart = null;

  constructor(private suffixPipe: NumberSuffixPipe) {}

  ngOnChanges(simple: SimpleChanges) {
    // check if current value is changed
    if (simple['chartData']?.currentValue != simple['chartData']?.previousValue) {
      if (this.chartRef) {
        this.chartRef.dispose();
      }
      this.createDonughtChart();
    }
  }

  createDonughtChart() {
    am4core.ready(() => {
      am4core.addLicense(config.amChartLicense);
      // Themes begin
      am4core.useTheme(am4themes_animated);
      // Themes end

      // Create chart instance
      this.chartRef = am4core.create('completedRideChart', am4charts.PieChart);
      this.chartRef.hiddenState.properties.opacity = 0; // this makes initial fade in effect

      // Add and configure Series
      const pieSeries = this.chartRef.series.push(new am4charts.PieSeries());
      pieSeries.dataFields.value = 'value';
      pieSeries.dataFields.category = 'name';

      // Let's cut a hole in our Pie chart the size of 30% the radius
      this.chartRef.innerRadius = am4core.percent(70);
      this.chartRef.colors.list = [am4core.color('#B45C08'), am4core.color('#434445'), am4core.color('#616163')];
      pieSeries.colors.list = [am4core.color('#B45C08'), am4core.color('#434445'), am4core.color('#616163')];
      // Put a thick dark border around each Slice
      pieSeries.slices.template.stroke = am4core.color('#25282d00');
      pieSeries.slices.template.strokeWidth = 2;
      pieSeries.slices.template.strokeOpacity = 1;
      pieSeries.slices.template.cornerRadius = 25;

      // change the cursor on hover to make it apparent the object can be interacted with
      pieSeries.slices.template.cursorOverStyle = [
        {
          property: 'cursor',
          value: 'pointer',
        },
      ];

      pieSeries.alignLabels = false;
      pieSeries.labels.template.radius = 3;

      pieSeries.ticks.template.disabled = true;
      pieSeries.labels.template.disabled = true;

      // Create a base filter effect (as if it's not there) for the hover to return to
      const shadow = pieSeries.slices.template.filters.push(new am4core.DropShadowFilter());
      shadow.opacity = 0;

      // Create hover state
      pieSeries.slices.template.states.getKey('hover'); // normally we have to create the hover state, in this case it already exists

      // Slightly shift the shadow and make it more prominent on hover

      // label in center
      // label in center
      const label = pieSeries.createChild(am4core.Label);
      label.text = this.suffixPipe.transform(this.totalCompletedRides ?? 0) + '\n Ride';
      label.horizontalCenter = 'middle';
      label.verticalCenter = 'middle';
      label.fontSize = 30;
      label.textAlign = 'middle';
      label.fontWeight = '100';
      label.fill = am4core.color('#ffffff');
      // Add a legend
      this.chartRef.legend = new am4charts.Legend();
      this.chartRef.legend.position = 'right';

      this.chartRef.data = this.chartData;
    });
  }
}
