import { formatDate } from '@angular/common';
/* eslint-disable no-undef */
import * as am4charts from '@amcharts/amcharts4/charts';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_dark from '@amcharts/amcharts4/themes/dark';
import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { config } from 'src/app/shared/app.config';
import { SessionService } from 'src/app/shared/services/session-service/session.service';

@Component({
  selector: 'app-total-income',
  templateUrl: './total-income.component.html',
  styleUrls: ['./total-income.component.scss'],
})
export class TotalIncomeComponent implements OnChanges {
  @Input() chartData = [];
  private chartRef: am4charts.XYChart = null;
  @Input() dateFormat = 'dd MMM';
  constructor(private session: SessionService) {}
  public currencySymbol = this.session.currentTenant.currency;

  ngOnChanges(simple: SimpleChanges) {
    // check if current value is changed
    if (simple['chartData'].currentValue != simple['chartData'].previousValue) {
      if (this.chartRef) {
        this.chartRef.dispose();
      }
      this.createChart();
    }
  }

  createChart() {
    am4core.ready(() => {
      am4core.addLicense(config.amChartLicense);
      // Themes begin
      am4core.useTheme(am4themes_dark);
      am4core.useTheme(am4themes_animated);
      // Themes end
      // Create chart
      this.chartRef = am4core.create('totalIncomeDiv', am4charts.XYChart);
      this.chartRef.hiddenState.properties.opacity = 0; // this makes initial fade in effect
      // Add data
      // Set input format for the dates
      // Create axes
      const dateAxis = this.chartRef.xAxes.push(new am4charts.DateAxis());
      dateAxis.renderer.grid.template.stroke = am4core.color('#1c1c21');
      dateAxis.renderer.grid.template.strokeOpacity = 0.8;
      dateAxis.renderer.labels.template.fill = am4core.color('#4D4D4D');
      // check if day format is set
      if (this.dateFormat.indexOf('EEE') > -1) {
        // check if am/pm is present
        if (this.dateFormat.indexOf('a') === -1) {
          this.chartRef.dateFormatter.inputDateFormat = 'dd-MM-yyyy';
          dateAxis.dateFormatter.dateFormat = 'dd-MM-yyyy hh:mm';
          dateAxis.dateFormats.setKey('hour', 'HH:mm');
        } else {
          dateAxis.renderer.labels.template.adapter.add('text', (text, target) => {
            if (target && target.dataItem && (target.dataItem as any).value) {
              const dateValue = formatDate(
                new Date((target.dataItem as any).value),
                this.session.timeFormat == '12' ? 'hh:mm a' : 'HH:mm',
                this.session.locale
              );
              return dateValue;
            }
            return text;
          });
          this.chartRef.dateFormatter.inputDateFormat = 'dd-MM-yyyy hh:mm a';
          dateAxis.dateFormatter.dateFormat = 'dd-MM-yyyy hh:mm a';
          dateAxis.dateFormats.setKey('hour', 'hh:mm a');
        }
      }

      // dateAxis.startLocation = 0;
      const valueAxis = this.chartRef.yAxes.push(new am4charts.ValueAxis());
      valueAxis.renderer.grid.template.stroke = am4core.color('#1c1c21');
      valueAxis.renderer.grid.template.strokeOpacity = 0.8;
      valueAxis.renderer.labels.template.fill = am4core.color('#4D4D4D');
      valueAxis.min = 0;
      valueAxis.maxPrecision = 0;
      valueAxis.numberFormatter.numberFormat = '#,##a';

      // min grid distance
      // valueAxis.renderer.minGridDistance = 50;
      // dateAxis.renderer.minGridDistance = 50;
      this.chartRef.numberFormatter.intlLocales = this.session.locale;
      this.chartRef.data = this.chartData;

      // Create series
      const series = this.chartRef.series.push(new am4charts.LineSeries());
      series.dataFields.valueY = 'value';
      series.dataFields.dateX = 'date';
      series.dataFields.id = 'displayName';
      series.tooltipText = '{id} \n[bold font-size: 14px]{value}';
      series.tooltip.label.wrap = true;
      series.minBulletDistance = 15;
      series.strokeWidth = 2;
      series.stroke = am4core.color('#D96B00');
      series.fill = am4core.color('#D96B00');
      series.fillOpacity = 0.6;

      const fillModifier = new am4core.LinearGradientModifier();
      fillModifier.opacities = [1, 0];
      fillModifier.offsets = [0, 1];
      fillModifier.gradient.rotation = 90;
      series.segments.template.fillModifier = fillModifier;

      // Drop-shaped tooltips
      series.tooltip.background.cornerRadius = 10;
      series.tooltip.background.strokeOpacity = 0;
      series.tooltip.pointerOrientation = 'vertical';
      series.tooltip.label.minWidth = 40;
      series.tooltip.label.minHeight = 40;
      series.tooltip.label.textAlign = 'middle';
      series.tooltip.background.fill = am4core.color('#232329');
      series.tooltip.getFillFromObject = false;
      // Make bullets grow on hover
      const bullet = series.bullets.push(new am4charts.CircleBullet());
      bullet.circle.strokeWidth = 2;
      bullet.circle.radius = 4;
      bullet.circle.fill = am4core.color('#25282D');

      const bullethover = bullet.states.create('hover');
      bullethover.properties.scale = 1.3;

      // Make a panning cursor
      this.chartRef.cursor = new am4charts.XYCursor();
      this.chartRef.cursor.behavior = 'panXY';
      this.chartRef.cursor.xAxis = dateAxis;
      this.chartRef.cursor.tooltipColorSource = series;

      this.chartRef.cursor.snapToSeries = series;
      this.chartRef.cursor.snapOnPan = true;
      this.chartRef.cursor.toBack();
      this.chartRef.cursor.tooltip.background.fill = am4core.color('#232329');
      this.chartRef.cursor.tooltip.getFillFromObject = false;

      // Create vertical scrollbar and place it before the value axis
      this.chartRef.scrollbarY = new am4core.Scrollbar();
      this.chartRef.scrollbarY.parent = this.chartRef.leftAxesContainer;
      this.chartRef.scrollbarY.toBack();

      // Create a horizontal scrollbar with previe and place it underneath the date axis
      // (this.chartRef.scrollbarX as any).series.push(series);
      // this.chartRef.scrollbarX.parent = this.chartRef.bottomAxesContainer;
      this.chartRef.scrollbarX = new am4core.Scrollbar();
      this.chartRef.scrollbarX.parent = this.chartRef.bottomAxesContainer;

      //  dateAxis.start = 0.79;
      dateAxis.keepSelection = true;
    });
    // Themes end

    // Add data
  }
}
