export interface IRideStatisticsQueryParam {
  timeZoneId: string;
  franchiseName?: string;
  periods?: rideStatisticTimeInterval;
  from?: string;
  to?: string;
}

export interface ITripDemandStatisticsQueryParam {
  from: string;
  to: string;
  timeZoneId: string;
  resolution: number;
  periods?: rideStatisticTimeInterval | null;
  franchiseName?: string;
}

export interface ITripDemandStatisticsResult {
  demands: ITripDemandCellSummary[];
}

export interface ITripDemandCellSummary {
  h3Cell: string;
  pickup: ITripDemandDetail;
  dropOff: ITripDemandDetail;
}

export interface ITripDemandDetail {
  count: number;
  rank: number;
}

export interface IAreaStatisticsQueryParam {
  timeZoneId: string;
  from?: string;
  to?: string;
  periods?: rideStatisticTimeInterval | null;
  franchiseName?: string;
  areaGroup?: number | null;
}

export interface IAreaStatisticsResult {
  timeSeries: IAreaStatisticsData[];
}

export interface IAreaStatisticsData {
  time: string;
  areaName: string;
  pickupCount: number;
}

export interface IRideStatisticsResult {
  summary: ISummary;
  timeSeries: ITimeSeries[];
  dailySummary: IDailySummary;
  tripSourceSummary: ITripSourceSummary[];
  franchiseSummary: ITripFranchiseSummary[];
}

export interface IRideStatistics {
  result: IRideStatisticsResult;
  interval: string;
}

export interface ITripSourceSummary {
  name: string;
  totalIncome: number;
  tripCount: number;
}

export interface ITripFranchiseSummary {
  name: string;
  tripCount: number;
  transitChargeCount: number;
  paidDistance: number;
}

export interface IDailySummary {
  averageIncome: number;
  averageIncomePerDriver: number;
  averageTripCount: number;
  averageTripPerCarCount: number;
  averageDistance: number;
  averagePaidDistance: number;
}

export interface ITimeSeries {
  time: string;
  data: {
    distance: number;
    duration: number;
    tripCount: number;
    carCount: number;
    driverCount: number;
    income: number;
    utilization: number;
  };
}

export interface ISummary {
  totalDistance: number;
  totalTripCount: number;
  totalIncome: number;
  averageTripPerCar: number;
  averageDistancePerCar: number;
  averageIncomePerCar: number;
  averageTripPerDriver: number;
  averageDistancePerDriver: number;
  averageIncomePerDriver: number;
  averageDistancePerTrip: number;
  averageIncomePerTrip: number;

  averageRevenuePerDistance: number;
  totalPaidDistance: number;
}

export type RideStatisticsInterval = 'Hourly' | 'Daily' | 'Monthly';

export interface IVehicleFranchise {
  Franchises: {
    Id: number;
    Name: string;
    IntergrationId: number;
  }[];
}

export interface IAreaGroupsResult {
  AreaGroups: {
    Id: number;
    Name: string;
  }[];
}

export interface IDriverStatisticsQueryParam {
  periods?: rideStatisticTimeInterval;
  timeZoneId: string;
  from?: string;
  to?: string;
  number: number;
  franchiseName?: string;
}

export interface IDriver {
  staffId: string;
  income: number;
  tripCount: number;
}

export interface IDriverStatistics {
  topTripDrivers: IDriver[];
  topIncomeDrivers: IDriver[];
}

export interface IChartData {
  date: string;
  value: number;
  displayName?: string;
}

export enum rideStatisticTimeInterval {
  LastDay = 1,
  LastWeek = 2,
  LastMonth = 3,
  Last3Month = 4,
}

export enum StatisticTimeInterval {
  Hourly = 1,
  Daily = 2,
  Monthly = 3,
}
