import { Component, Input, OnInit } from '@angular/core';
import { SessionService } from 'src/app/shared/services/session-service/session.service';
import { ITripFranchiseSummary } from '../../models/dashboard.interface';

@Component({
  selector: 'app-franchise-summary',
  templateUrl: './franchise-summary.component.html',
  styleUrls: ['./franchise-summary.component.scss']
})

export class FranchiseSummaryComponent implements OnInit {
  @Input() data: ITripFranchiseSummary[];
  public lengthUnit = this.session.lengthUnit;
  public totalSummary: ITripFranchiseSummary = {
    name: 'Total',
    tripCount: 0,
    transitChargeCount: 0,
    paidDistance: 0,
  };

  constructor(private session: SessionService) { }

  ngOnInit() {
    this.data.forEach((item) => {
      this.totalSummary.tripCount += item.tripCount;
      this.totalSummary.transitChargeCount += item.transitChargeCount;
      this.totalSummary.paidDistance += item.paidDistance;
    });
  }

  exportCsv(tableSelector) {
    let data = "";
    const tableData = [];
    const rows = document
      .querySelector(tableSelector)
      .querySelectorAll(" tr");

    for (const row of rows) {
      // skip empty rows
      if(!row.innerText || row.innerText == "") continue;

      const rowData = [];
      const columns = row.querySelectorAll("th, td");

      for (const column of columns) {
        const text = column.innerText ?? "";
        rowData.push(text.includes(',') ? `"${text}"` : text);
      }

      tableData.push(rowData.join(","));
    }

    data += tableData.join("\n");
    const blob = new Blob([data], { type: "text/csv" });
    this.downloadBlob(blob, "franchise-summary.csv");
  }

  downloadBlob(blob: Blob, fileName: string) {
    const a = document.createElement("a");
    a.href = URL.createObjectURL(blob);
    a.setAttribute("download", fileName);
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }
}
