import { H3HelperService, IH3HelperCellLiteral } from 'src/app/shared/services/h3-helper/h3-helper.service';
import { ITripDemandStatisticsQueryParam, ITripDemandStatisticsResult } from '../models/dashboard.interface';

export class DemandLoaderService {
  private readonly cellGroupId = "demand";
  private currentDemandType: DemandType;
  private h3Helper: H3HelperService;

  constructor(public map: google.maps.Map, public config: IDemandLoaderConfig) {
    this.currentDemandType = config.defaultDemandType;
    this.h3Helper = new H3HelperService(map, {
      styleFunction: this.getStyle.bind(this)
    });
  }

  async loadDemand(): Promise<boolean> {
    const params = new URLSearchParams({
      "from": this.config.query.from,
      "to": this.config.query.to,
      "timeZoneId": this.config.query.timeZoneId,
      "resolution": this.config.query.resolution.toString()
    });

    if (this.config.query.periods)
      params.append("periods", this.config.query.periods.toString());

    if (this.config.query.franchiseName)
      params.append("franchiseName", this.config.query.franchiseName);

    const response = await fetch(`${this.config.baseUrl}?${params.toString()}`);
    if (!response.ok)
      return false;

    const result: ITripDemandStatisticsResult = await response.json();

    const cells: IH3HelperCellLiteral[] = result.demands.map(demand => ({
      cell: demand.h3Cell,
      properties: {
        [DemandTypes.Pickup]: demand.pickup,
        [DemandTypes.Dropoff]: demand.dropOff
      }
    }));

    if (cells.length === 0) {
      return false;
    }

    this.h3Helper.renderCellsAsGroup(this.cellGroupId, cells, this.config.dataLayerConfigurer);

    this.map.setCenter(H3HelperService.GetH3CellLatLng(result.demands[0].h3Cell));

    return true;
  }

  setDemandType(demandType: DemandType) {
    this.currentDemandType = demandType;
    this.h3Helper.refreshGroupStyle(this.cellGroupId);
  }

  getDemandType(): DemandType { return this.currentDemandType; }

  private getStyle(feature: google.maps.Data.Feature): google.maps.Data.StyleOptions {
    const property = feature.getProperty(this.getDemandType());

    const styleOptions: google.maps.Data.StyleOptions = {
      strokeColor: "lightgrey",
      strokeWeight: 0.1,
      fillOpacity: 0.3
    };

    if (!property || property.count == 0) {
      styleOptions.fillOpacity = 0;
      styleOptions.zIndex = -9999;
      styleOptions.clickable = false;
    } else if (property.rank <= 1) {
      styleOptions.fillColor = "#579242";
    } else if (property.rank <= 5) {
      styleOptions.fillColor = "#279200";
    } else if (property.rank <= 10) {
      styleOptions.fillColor = "#edf600";
    } else if (property.rank <= 20) {
      styleOptions.fillColor = "#ffcf00";
    } else if (property.rank <= 50) {
      styleOptions.fillColor = "#ff7500";
    } else if (property.rank <= 75) {
      styleOptions.fillColor = "#ff2000";
    } else {
      styleOptions.fillColor = "#980067";
    }

    return styleOptions;
  }
}

export const DemandTypes = {
  Pickup: "pickup",
  Dropoff: "dropoff"
} as const;

export type DemandType = typeof DemandTypes[keyof typeof DemandTypes];

export interface IDemandLoaderConfig {
  baseUrl: string;
  query: ITripDemandStatisticsQueryParam
  defaultDemandType: DemandType;
  dataLayerConfigurer: (dataLayer: google.maps.Data) => void;
}